import React, {Component} from 'react';
import { Route } from 'react-router-dom';

import AnimatedSwitch from '../../components/animated-switch';

import Performance from './Performance';
import Students from './Students';
import Classes from './Classes';
import Expenditure from './Expenditure';
import Debts from './Debts';
import Teachers from './Teachers';
import Staffs from './Staffs';
import Members from './Members';

export default class History extends Component {
    render() {
        let match = this.props.match;
        return <AnimatedSwitch>
            <Route path={`${match.path}/performance`} render={(props) => <Performance {...props} /> } />
            <Route path={`${match.path}/students`} render={(props) => <Students {...props} /> } />
            <Route path={`${match.path}/classes`} render={(props) => <Classes {...props} /> } />
            <Route path={`${match.path}/expenditure`} render={(props) => <Expenditure {...props} /> } />
            <Route path={`${match.path}/debts`} render={(props) => <Debts {...props} /> } />
            <Route path={`${match.path}/teachers`} render={(props) => <Teachers {...props} /> } />
            <Route path={`${match.path}/staffs`} render={(props) => <Staffs {...props} /> } />
            <Route path={`${match.path}/members`} render={(props) => <Members {...props} /> } />
        </AnimatedSwitch>
    }
}