import React, { Component } from 'react';
import axios from 'axios';

import DataTable from '../../components/DataTable';
import getCookie from '../../utils/getCookie';

export default class Staffs extends Component {
    state = {};
    constructor(props) {
        super(props);
        let self = this;
        this.dataTable = React.createRef();
        this.dataTableConfig = {
            serverSide: true,
            processing: true,
            ajax: function(params, callback, settings) {
                params.period_id = self.state.period_id;
                axios.get('/api/history/staffs', {params: {params}}).then(response=>{
                    callback(response.data);
                })
            },
            columns: [
                { data: 'staff_no', name: 'staff_no', 'class': 'text-center'},
                { data: 'firstname', name: 'firstname', 'class': 'text-center'},
                { data: 'lastname', name: 'lastname', 'class': 'text-center'},
                { data: 'id_no', name: 'id_no', 'class': 'text-center'},
                { data: 'phone', name: 'phone', 'class': 'text-center'},
                { data: 'email', name: 'email', 'class': 'text-center'},
                { data: 'date_of_employment', name: 'date_of_employment', 'class': 'text-center', render: (data,type,row) => (new Date(data)).toDateString() },
                { data: 'date_left', name: 'date_left', 'class': 'text-center', render: (data,type,row) => data ? (new Date(data)).toDateString() : '-' },
            ],
        };
    }

    componentDidMount() {
        axios.get('/api/history/periods').then(res=>{
          this.setState({periods: res.data});
        })
    }

    inputChanged = (event) => {
        let val = event.target.value;
        let field = event.target.name;
        this.setState({[field]: val}, this.filterChanged);
    }

    filterChanged = () => {
        this.dataTable.current.draw();
    }

	render() {
        let periods = this.state.periods;
		return <div className="container-fluid">
            <DataTable ref={this.dataTable} title="Staffs" config={this.dataTableConfig} toolbar={
                <>
                  <select onChange={this.inputChanged} name="period_id" value={this.state.period_id} id='filterByPeriod' className="form-control col-12">
                    <option value=''>-- Select Period --</option>
                    {periods && periods.map( (period) => <option key={period.id} value={period.id}>{period.name}</option> )}
                  </select>
                </>
                }>
                <th>Staff No</th>
                <th>Firstname</th>
                <th>Lastname</th>
                <th>ID Number</th>
                <th>Phonenumber</th>
                <th>Email</th>
                <th>Date of Employment</th>
                <th>Date Left</th>
            </DataTable>
		</div>

	}
}
