import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import axios from 'axios';

import DataTable from '../../components/DataTable';
import getCookie from '../../utils/getCookie';

export default class Performance extends Component {
  constructor(props) {
    super(props);
    var self = this;
    this.dataTable = React.createRef();
    this.state = {
      columns: [],
      dataTableConfig: {
        serverSide: true,
        processing: true,
        ajax: function(params, callback, settings) {
            params.period_id = self.state.period_id;
            params.class_id = self.state.class_id;
            params.stream_id = self.state.stream_id;
            axios.get('/api/history/performance', {params: {params}}).then(response=>{
                callback(response.data);
            })
        },
        columns: []
      }
    };
  }
  componentDidMount() {
    let columns = [
      { data: 'firstname', name: 'firstname', 'class': 'text-center editable'},
      { data: 'lastname', name: 'lastname', 'class': 'text-center editable'},
    ];
    axios.get('/api/setup/subjects').then(res=>{
      for(let subject in res.data.data) {
        let sub = res.data.data[subject];
        columns.push({data: ( row, type, set, meta ) => {
          if(!row[sub.shorthand])
            return '-';

          return row[`${sub.shorthand}_grade`];
        }, name: sub.shorthand, class: 'text-center editable'})
      }
      columns.push({data: ( row, type, set, meta ) => {
        if(!row['total'])
          return '-';

        return row['grade'];
      }, name: 'total', class: 'text-center editable'},
      {
        data: null, name:'Action', 'class': 'text-center', render: function(data,type,row) {
              return `<span>
                <a href="/report-card/${data.id}" target="_blank" class="btn btn-link btn-warning"><i class="fa fa-print"></i></a>
            </span>`
          }
      })

      let dataTableConfig = this.state.dataTableConfig;
      dataTableConfig.columns = columns;
      this.setState({dataTableConfig});
    });

    axios.get('/api/history/classes-all').then(res=>{
      this.setState({classes: res.data});
    });

    axios.get('/api/history/periods').then(res=>{
      this.setState({periods: res.data});
    })
  }

  inputChanged = (event) => {
    let val = event.target.value;
    let field = event.target.name;
    if(field == 'stream_id')
      this.setState({stream_id: val}, this.filterChanged);
    else if(field == 'class_id')
      this.setState({class_id: val, stream_id: ''}, this.filterChanged);
    else
      this.setState({[field]: val}, this.filterChanged);
  }

  filterChanged = () => {
    this.dataTable.current.draw();
  }

  render() {
    let periods = this.state.periods;
    let classes = this.state.classes;
    let streams = null;
    if(classes) {
      let selectedClass = classes.find( (cls) => cls.id == this.state.class_id );
      if(selectedClass) {
        streams = selectedClass.streams;
      }
    }

    return <div className="container-fluid">
      <div className="row">
        <div className="col-md-12">
        </div>
      </div>
      { this.state.dataTableConfig.columns.length && <DataTable ref={this.dataTable} title="Performance" config={this.state.dataTableConfig} toolbar={
        <>
          <select onChange={this.inputChanged} name="period_id" value={this.state.period_id} id='filterByPeriod' className="form-control col-4">
            <option value=''>-- Select Period --</option>
            {periods && periods.map( (period) => <option key={period.id} value={period.id}>{period.name}</option> )}
          </select>
          <select onChange={this.inputChanged} name="class_id" value={this.state.class_id} id='filterByClass' className="form-control col-4">
            <option value=''>-- Select Classes--</option>
            {classes && classes.map( (cls) => <option key={cls.id} value={cls.id}>{cls.name}</option> )}
          </select>
          <select onChange={this.inputChanged} name="stream_id" value={this.state.stream_id} id='filterByStream' className="form-control col-4">
            <option value=''>-- Select Stream--</option>
            {streams && streams.map( (stream) => <option key={stream.id} value={stream.id}>{stream.name}</option> )}
          </select>
        </>
      }>
        {this.state.dataTableConfig.columns.map( (value,index) => <th key={index}>{value.name}</th> )}
      </DataTable> }
    </div>

  }
}
